$메인칼라 : #ff0000;


.red {

    color: $메인칼라;
}

//내가 축약하고 싶은 긴 문자를 담는다.
//mixin 문법
@mixin 함수() {
    
    background-color: #eeeeee;
    padding:  20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;


}




.my-alert {

    @include 함수()
}

// my-alert에 있는 속성을 my-alert2에 붙혀넣어주세요 라는 뜻
.my-alert2 { 

    @extend .my-alert;
    background: #ffe591;

}








.my-alert p {
    margin-bottom: 0;
}

// 이게 nesting 문법이다.
div.container{

    h4{
        color : blue;
    }


}